<template lang="">
    <article class="md:max-w-18rem">
        <figure class="pb-3">
            <img class="" :src="item.img" :alt="item.pole" />
        </figure>
        
        <div        
        class="py-3 border-[1pt] border-blue_1"        
        >
            <h3 class="text-xl font-black uppercase xs:text-3xl md:text-4xl text-dark_blue_1 font-DIN-Medium">{{ item.name }}</h3>
            <span class="text-3xl text-dark_blue_1">{{ item.country }}</span>
            
            <span class="block uppercase font-DIN-Black text-orange_1 justify-center">desde</span>

            <div class="flex flex-row h-6 md:h-10 lg:h-12 justify-items-center">

              <!-- titulo o precio de la oferta -->
              <div v-if="(item.name === 'Tramites')" class="relative flex justify-center flex-1">
                <span
                    class="absolute z-10 block pt-4 text-3xl leading-3 price font-DIN-Black text-blue_1 xs:text-4xl md:text-5xl"
                >${{ item.price }}&nbsp;OFF                                                                      
                </span>
              </div>

              <div v-else class="relative flex justify-center flex-1">                
                <span
                    class="absolute z-10 block pt-4 text-3xl leading-3 price font-DIN-Black text-blue_1 xs:text-4xl md:text-5xl"
                >${{ item.price }}                                                                      
                </span>
              </div>            
                
            </div>

            <!-- Descripcion de la oferta -->
            <p class="px-1 md:px-4 py-3 pt-6 text-sm offer-description text-blue_1 bg-light_gray_2 lg:text-base" style="height: 100px">{{ item.description }}</p>

            <small class="flex px-3 md:px-5">{{ item.info }}</small>
        </div>
        <button
        class="w-full my-5 text-sm btn-booking font-DIN-Bold md:text-2xl"
        @click="goOffer(item.keyword, {
            country: 'Cuba',
            product: item.name,
            product_name: item.description,
            price: item.price,
        })"
        >{{ item.btn }}</button>
    </article>
</template>
<script>

export default {
  name: "OfferComp",
  props: {
    item: {},
  },  
  methods: {
    goOffer(keyword, params) {
      this.$emit("offer", { keyword, params });
    },
  },
};
</script>
<style scoped>
article {
  width: 45%;
}
@media screen and (max-width: 520px) {
  p {
    height: 100px !important;
  }
}

.btn-booking {
  background-color: var(--blue_1);  
  border: 1pt solid var(--blue_1);
  color: white;
  text-transform: uppercase;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
}
.btn-booking:hover {
  background-color: white;    
  color: var(--blue_1); 
  border: 1pt solid var(--blue_1);   
}
</style>