<template>
  <div class="bg-blue_1">
    
    <div class="">
      <div class="w-full h-full">
        <Splide class="w-full h-full" :options="slideoptions">            
          <SplideSlide v-for="(item, index) in poles" :key="index">
            <div class="w-full h-full flex items-center justify-center relative">
              <h2 class="absolute py-6 text-4xl md:text-6xl lg:text-4xl z-10 top-5 lg:top-16">- {{ title }} -</h2>
              <figure class="w-full h-[400px] lg:h-full relative overflow-hidden">
              <img
              class="w-full h-full object-cover"
              :src="item.img"
              alt="polos"
              @click="goSinglePole(item.keyword, {
                country: item.name + ' - ' + item.country,
                pole: item.name,
              })"
              />
              <figcaption
              class="absolute top-12 left-0 w-full h-full flex flex-col items-center justify-center text-white text-6xl font-DIN-Black"
              >
              {{ item.name }}
              <div class="relative w-40 lg:w-[300px]">
                  <p class="font-DIN-Light text-base lg:text-2xl line-clamp-2 my-5">{{ item.description }}</p>                  
                  <button
                  class="text-white uppercase btn-destinos bg-orange_1 font-DIN-Medium text-xs lg:text-3xl w-full py-2 tracking-widest"
                  @click="goSinglePole(item.keyword, {
                    country: item.name + ' - ' + item.country,
                    pole: item.name,
                  })"
                  >
                  descubre
                  </button>
              </div>
              </figcaption>
            </figure>            
            </div>
          </SplideSlide>
          
        </Splide>
        <div class="splide__arrows">          
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import config from "@/config";
import { Splide, SplideSlide } from "@splidejs/vue-splide";
import "@splidejs/splide/dist/css/themes/splide-default.min.css";

const SLIDECONFIG = {
direction: "ltr",
rewind: true,
wheel: false,
pagination: false,
autoplay: true,
height: "auto",
width: "100%",
arrows: true,
arrowScale: 1.2,
};

export default {
name: "PoleSlide",
props: {
  title: {
    default: "",
  },
},
components: {
  Splide,
  SplideSlide,
},
data() {
  return {
    slideoptions: SLIDECONFIG,
    poles: [ 
      {
        img: config.gallery_url + "general/new_destino1.jpg",
        country: "Cuba",
        description: "Disfruta una escapada familiar inolvidable",
        name: "Cuba",
        keyword: "cuba",
      },
      {
        img: config.gallery_url + "general/new_destino2.jpg",
        country: "Mexico",
        description: "Conoce la mejor playa de México",
        name: "Cancun",
        keyword: "RQ",
      },
      {
        img: config.gallery_url + "general/new_destino3.jpg",
        country: "Republica Dominicana",
        name: "Punta Cana",
        description: "Hoteles para todo presupuesto",
        keyword: "RQ",
      },
      ],
    };
  },
  methods: {
      goSinglePole(keyword, params) {
        if (keyword == "RQ") {
          this.$emit('RQsinglePole',params);
        } else {
          this.$router.push({
            name: "SinglePole",
            params: { keyword }
          });
        }
      },
    }
};
</script>
<style scoped>
/deep/ .splide__arrow svg {
  fill: #fff;
}

/deep/ .splide__arrow{
  background-color: inherit;
}

.splidearrow::before {
  filter: opacity(0.74) hue-rotate(180deg);
}

/* Add this style to your scoped styles */
/deep/ .splide__arrow {
  color: #fff;
}

/deep/ .splide__arrow::before {
  filter: opacity(0.74) hue-rotate(180deg);
}

/* Forcing the styles to the original component */
/* Desktop view styles */
@media (min-width: 1024px) {
  /deep/ .splide__arrow--prev {
    left: 10em;
  }

  /deep/ .splide__arrow--next {
    right: 10rem;
  }
}

/* Mobile view styles */
@media (max-width: 1023px) {
  /deep/ .splide__arrow--prev {
    left: 1em; /* Change this value as needed */
  }

  /deep/ .splide__arrow--next {
    right: 1rem; /* Change this value as needed */
  }
}
