<template>
  <div>
      <section class="bg-white">
         <h2 class="text-3xl pt-6 text-blue_1 
         md:text-4xl
         lg:text-3xl">- Quienes Somos -</h2>

         <span class="font-Clarissa text-5xl">{{country}}</span>

         <!-- BUSCADOR -->         
         <searcher></searcher>
      </section>

      <section class="bg-white text-white">
         <!-- <figure>
            <img src="@/assets/img/singlepole-top-blue.png" alt="" />
         </figure> -->
 
         <div class="bg-blue_1 pb-10">
            <article class="py-10 m-auto md:w-8/12 text-justify font-DIN px-4 md:px-0 text-sm md:text-xl">
              <p class="text-justify font-DIN text-xl" v-html="about_text"></p>            
            </article>
         </div>
      </section>
   </div>
</template>
<script>
  import textServices from "@/services/common/texts.service";
  import Searcher from "@/components/searcher/Searcher.vue";

  export default {
    name: 'AboutComp',
    components: {Searcher},
    data() {
      return {
        about_text: ''
      }
    },
    created() {
      textServices.getText("about-home").then(t => {
        this.about_text = t
          ? t.description.replace(/\r?\n/g, "<br>")
          : "";
      });
    },
  }
</script>
