<template>
  <div>
    <!-- Buscador -->
    <section>
      <searcher></searcher>
    </section>

    <!-- Poles -->
    <section class="bg-white text-white">
      <!-- <figure>
        <img src="@/assets/img/home-wave-top.png" alt />
      </figure> -->
        <pole-slide :title="'Destinos'" @RQsinglePole="goSinglePole"></pole-slide>
      <!-- <figure>
        <img src="@/assets/img/home-wave-bottom.png" alt />
      </figure> -->
    </section>

    <!-- offerts -->
    <section class="bg-white py-20 px-3 md:px-10">
      <h2 class="text-3xl py-6 text-dark_blue_1 md:text-6xl lg:text-4xl font-DIN-Bold uppercase">- Ofertas Express -</h2>

      <div class="flex flex-row justify-center flex-wrap gap-3 md:gap-6 md:px-4 sm:flex-row sm:px-10">
        <offer :item="item" v-for="(item, index) in offerts" :key="index" @offer="goOffer"></offer>
      </div>
    </section>

    <!--about us  -->
    <section class="flex flex-col mx-auto w-10/12 mb-10">
      
      <h2
        class="uppercase text-xl py-6 text-dark_blue_1 sm:text-2xl lg:text-4xl font-DIN-Bold tracking-widest mb-10"
      >- bienvenido a iris travel -</h2>

      <div class="grid grid-cols-1 md:grid-cols-2 gap-5">
        <p class="text-justify font-DIN-Regular text-xs lg:text-xl w-full text-dark_blue_1 leading-7 lg:leading-10 lg:px-10" v-html="about_text"></p>
      
        <figure class="w-full">
          <img :src="about_img" alt="">
        </figure>
      </div>

    </section>
  </div>
</template>
<script>
import config from "@/config";
import Searcher from "@/components/searcher/Searcher.vue";
import HomeService from "@/services/home.service";
import textServices from "@/services/common/texts.service";
import Offer from "../components/home/Offer.vue";
import PoleSlide from "../components/home/PoleSlide.vue";
// import "@/assets/css/slide-home.css";
// import { json } from "body-parser";

export default {
  name: "HomeView",
  data() {
    return {           
      offerts: [],
      about_img: config.gallery_url + "general/about-me.jpg",
      about_text: ""
    };
  },
  created() {
    const init = [];
    localStorage.removeItem('request_info')
    init.push(textServices.getText("about-home"));
    init.push(HomeService.getOffers());
    Promise.all(init).then(res => {
      if (res) {
        this.about_text = res[0]
          ? res[0].description.replace(/\r?\n/g, "<br>")
          : "";
        this.offerts = res[1];
      }
    });
  },
  components: {
    Searcher,    
    Offer,
    PoleSlide,
  },
  methods: {
    goSinglePole(params) {
      this.sendRequest(params);      
    },
    goOffer({keyword, params}) {
      if (keyword == "RQ") { //los casos que tengan RQ es directo a la pagina de request (envio de solicitud)        
        this.sendRequest(params);
      } else {
        this.$router.push({
          name: "Products",
          params: { 
            poleKey: 'cuba',
            productKey: keyword
          }
        });
      }
    },
    sendRequest(params) {
      localStorage.removeItem('request_info')
      const request = {
        type: 'home',
        destination: ('country' in params) ? params.country : 'Cuba',
        product: ('product' in params) ? params.product : 'Hoteles',                        
        initprice: ('product_name' in params) ? params.price : 0,                
        price: ('product_name' in params) ? params.price : 0,                
        pole: ('pole' in params) ? params.pole : '',
        date: ('date' in params) ? params.date : '1900-01-01/1900-01-05',
        name: ('product_name' in params) ? params.product_name : '',
        num_person: 0,
        num_rooms: 0,
      }        
      localStorage.setItem('request_info', JSON.stringify(request))
      this.$router.push({ name: "SendRequest" });
    }
  }
};
</script>
<style scoped>
.price {
  text-shadow: -2px 2px #232e6e;
}
.about .container {
  top: 80px;
  left: 10em;
}

@media screen and (max-width: 640px) {
  .about .container {
    top: 10px;
    left: 0em;
  }
}
.offer-description {
  max-height: 120px !important;
}
</style>
